// 
function getTime(defaultValue) {
  let [age, month, day] = String(defaultValue.split('-'))
    ? defaultValue.split('-')
    : ['2020', '01', '01'];
  day = day === undefined ? '01' : day;
  month = month === undefined ? '01' : month;
  const status =
    !isNaN(age) &&
    age.length === 4 &&
    !isNaN(month) &&
    month.length === 2 &&
    Number(month) < 13 &&
    !isNaN(day) &&
    day.length === 2
      ? 'normal'
      : 'error';

  day =
    !isNaN(age) &&
    age.length === 4 &&
    !isNaN(month) &&
    month.length === 2 &&
    !isNaN(day) &&
    day.length === 2
      ? day
      : '01'; // 
  month =
    !isNaN(age) &&
    age.length === 4 &&
    !isNaN(month) &&
    month.length === 2 &&
    Number(month) < 13
      ? month
      : '01'; // 
  age = !isNaN(age) && age.length === 4 ? age : '2020'; // 
  return [age, month, day, status];
}
