export default function DatePicker(props) {
  const {
    className,
    placeholder,
    starAge,
    endAge,
    style,
    startMonth = 1,
    endMonth = 12,
    defaultValue = moment().format('YYYY-MM-DD'),
    show = false,
    onChange = () => {},
  } = props;

  /*  */
  // const [
  //   ageList, // 
  //   mouthList, // 12
  //   weekList, // 
  //   selectedAge, // 
  //   setSelectedAge, // hook
  //   selectedMouth, // 
  //   setSelectedMouth, // hook
  //   selectedDay, // 
  //   setSelectedDay, // hook
  //   week, // 
  //   endDay, // 
  //   // inputValue, // 
  //   // setInputValue, // hook
  //   // status, // 
  //   // setStatus, // hook
  // ] = calculate(
  //   defaultValue,
  //   placeholder,
  //   startMonth,
  //   endMonth,
  //   starAge,
  //   endAge
  // );
  const [Tage, Tmonth, Tday, Status] = getTime(defaultValue); // 
  const ageList = Vdata(starAge, endAge); // 
  const mouthList = Vdata(startMonth, endMonth); // 12
  const weekList = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday',
  ]; // 

  const [selectedAge, setSelectedAge] = useState(Tage); // 
  const [selectedMouth, setSelectedMouth] = useState(Tmonth); // 
  const [selectedDay, setSelectedDay] = useState(Tday); // 
  const _month = moment(`${selectedAge}-${selectedMouth}`); // 
  // const startDay = _month.startOf('month').format('DD');//
  const endDay = Number(_month.endOf('month').format('DD')); //  , Number 
  const week = _month.startOf('month').format('dddd'); // 
  const _value = placeholder ? '' : defaultValue;
  const _Status = placeholder ? 'normal' : Status;
  const [inputValue, setInputValue] = useState(_value); // input
  const [status, setStatus] = useState(_Status); // 
  //  - 
  const FselectedAge = (e, itemAge) => {
    const value = `${itemAge}-${VpadLeft(selectedMouth)}-${VpadLeft(
      selectedDay
    )}`;
    setSelectedAge(itemAge); // 
    setStatus('false'); // 
    setInputValue(value); // input 
    e.preventDefault(); // 
  };
  //  - 
  const FselectedMouth = (e, itemMouth) => {
    const value = `${selectedAge}-${VpadLeft(itemMouth)}-${VpadLeft(
      selectedDay
    )}`;
    setSelectedMouth(itemMouth); // 
    setInputValue(value); // input 
    setStatus('false'); // 
    e.preventDefault(); // 
  };

  //  - 
  const FselectedDay = (event, itemDay) => {
    const value = `${selectedAge}-${VpadLeft(selectedMouth)}-${VpadLeft(
      itemDay
    )}`;
    setSelectedDay(itemDay); // 
    setInputValue(value); // input 
    setStatus('false'); // 
    setIsShow(false); // 
    event.preventDefault(); // 
  };
  // ,
  Stf(status);

  // input
  onChange(selectedAge, selectedMouth, VpadLeft(selectedDay));

  // 
  const [isShow, setIsShow] = useState(show);

  const close = () => {
    setIsShow(false); // 
    return isShow;
  };

  // class
  const cls = classNames(className, `${Variable}-datepicker`);
  return (
    <Fragment>
      <Overlay
        show={show}
        close={close}
        cuClassName={cls}
        cuStyle={style}
        render={
          <div className={`${Variable}-calendar`}>
            <div className={`${Variable}-calendar__top`}>
              <div className={`${Variable}-calendar__top-text`}>
                <span>{selectedAge}</span>
                <span>{VpadLeft(selectedMouth)}</span>
                <span>{VpadLeft(selectedDay)}</span>
              </div>
              <div className={`${Variable}-calendar__top-op`}>
                <Icon type="close" hover size="16" onClick={close} />
              </div>
            </div>
            <div className={`${Variable}-calendar__body`}>
              {/*  */}
              <div className={`${Variable}-calendar__age`}>
                {ageList.map((itemAge, index) => (
                  <div
                    className={classNames(
                      `${Variable}-calendar__row`,
                      itemAge === Number(selectedAge) ? 'is-selected' : ''
                    )}
                    key={index}
                    onClick={(event) => FselectedAge(event, itemAge)}
                  >
                    <div className={`${Variable}-calendar__cell`}>
                      <span>{itemAge}</span>
                    </div>
                  </div>
                ))}
              </div>
              {/*  */}
              <div className={`${Variable}-calendar__month`}>
                {mouthList.map((itemMouth, index) => (
                  <div
                    className={classNames(
                      `${Variable}-calendar__row`,
                      itemMouth === Number(selectedMouth) ? 'is-selected' : ''
                    )}
                    key={index}
                    onClick={(event) =>
                      FselectedMouth(event, VpadLeft(itemMouth))
                    }
                  >
                    <div className={`${Variable}-calendar__cell`}>
                      <span>{VpadLeft(itemMouth)}</span>
                    </div>
                  </div>
                ))}
              </div>
              {/*  */}
              <div className={`${Variable}-calendar__day`}>
                <div className={`${Variable}-calendar__day-header`}>
                  <div className={`${Variable}-calendar__row`}>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span></span>
                    </div>
                  </div>
                </div>
                <div className={`${Variable}-calendar__day-body`}>
                  <div className={`${Variable}-calendar__row`}>
                    {weekList.map(
                      (item, index) =>
                        item === week &&
                        [...Array(index)].map((item, index) => (
                          <div
                            className={`${Variable}-calendar__cell`}
                            key={index}
                          >
                            <span></span>
                          </div>
                        ))
                    )}
                    {!!endDay &&
                      [...Array(endDay)].map((itemDay, index) => (
                        <div
                          className={classNames(
                            `${Variable}-calendar__cell`,
                            Number(index + 1) === Number(selectedDay)
                              ? 'is-selected'
                              : ''
                          )}
                          key={index}
                          onClick={(event) =>
                            FselectedDay(event, Number(index + 1))
                          }
                        >
                          <span>{index + 1}</span>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        }
      >
        <Fragment>
          <div className={`${Variable}-datepicker__input`}>
            <Input
              placeholder={placeholder}
              value={inputValue}
              onChange={onChange}
            />
            <Icon type="date" size="16" />
          </div>
        </Fragment>
      </Overlay>
    </Fragment>
  );
}
